.formula-modal-body-title{
		border-bottom: 1px solid #ddd;
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
}
.formula-modal-body-title div span {
	cursor: pointer;
}

/*左侧tabs*/
.formula-modal-field-wrap{
	border: 1px solid #ddd;
	border-radius: 3px;
	height: 400px;
	width: 160px;
    overflow: hidden;
}
.formula-modal-field-list{
    height: 360px;
    overflow: auto;
		border-right: none;
}
.ant-menu-submenu-inline .ant-menu-submenu-title{
    padding: 0 40px  0 10px!important;
    border: 0;
    border-bottom: 1px solid #ddd;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -ms-text-overflow: ellipsis;
    cursor: pointer;
}
.ant-menu-sub .ant-menu-item{
    padding: 0 10px 0 20px !important;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -ms-text-overflow: ellipsis;
    cursor: pointer;
    border: 0;
}
.ant-menu-inline .ant-menu-item, .ant-menu-vertical .ant-menu-item {
    left: 0;
}
.ant-menu-submenu-inline.ant-menu-submenu-open.ant-menu-submenu{
    border-bottom: 1px solid #ddd;
}

/*中间*/
.formula-modal-edit-wrap{
    padding: 0 5px 0 15px;
}
.formula-modal-edit-title{
    border: 1px solid #ddd;
    border-bottom: 0;
    height: 40px;
    line-height: 40px;
    padding-left: 10px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.formula-edit-textarea{
    width: 100%;
    height: 280px;
    padding: 8px 10px;
    border: 1px solid #ddd;
    margin-bottom: 5px;
    resize: none;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
}
.formula-edit-textarea:focus {
	outline: none;
}

/*操作符*/
.formula-oprator-items{
    display: inline-block;
    width: 35px;
    height: 27px;
    text-align: center;
    line-height: 27px;
    background: #efefef;
    margin: 6px 7px;
    border: 1px solid #ddd;
    border-radius: 3px;
    cursor: pointer;
}

/*右侧*/
.formula-modal-funclist-wrap{
	width: 235px;
	height: 410px;
	float: right;
	overflow: auto;
}
.formula-modal-funclist-title{
    border: 1px solid #ddd;
    border-bottom: 0;
    height: 40px;
    line-height: 40px;
    padding-left: 10px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}
.formula-panel-collapse-wrap{
	width: 234px;
	height: 280px;
	overflow: auto;
    margin-bottom: 10px;
    border: 1px solid #ddd;
}

.formula-panel-collapse{
	overflow: auto;
    border-left: 0;
    border-right:  0;
}
.formula-panel-collapse-titles{
	background: #efefef;
}
.formula-panel-collapse-titles div{
	padding: 0;
	border: 0;
}
.formula-panel-collapse-titles ul{
    margin: -16px 0 -17px 0;
}
.ant-collapse {
     border: 0;
     border-bottom: 1px solid #ddd;
}
.formula-panel-collapse-items{
	height: 35px;
    line-height: 35px;
    border-bottom: 1px solid #ddd;
    padding: 0px 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
	-ms-text-overflow: ellipsis;
    cursor: pointer;
}
.formula-panel-function-tips{
	padding: 5px 10px;
	border-radius: 3px;
	border: 1px solid #ddd;
	height: 80px;
	width:  235px;
	overflow: auto;
}
.formula-panel-tips-title{
    color: #dd5858;
}
.formula-panel-tips-content{
	font-size: 11px;
	line-height: 1.5em;
}

/*滚动条*/
.thin-scroll::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}
.thin-scroll::-webkit-scrollbar-thumb {
    background-color: #A6A6A6;
    border-left: 2px solid transparent;
}
.thin-scroll::-webkit-scrollbar-track {
    background-color: #E5E5E5;
    border-left: 2px solid transparent;
}

h1, h2, h3, h4, h5, h6 {
	 margin-bottom: 0
}

.formula-panel-collapse-titles ul {
    margin: -16px 0 -17px -50px;
}

div.formula-panel-collapse{
		overflow: hidden;
}
